.signupWrapper{
  display: flex;
  justify-content: center;
}
.signupCard{
  width: 27vw;
  margin-top: 5%;
}
.insta-logo{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: contain;
  margin-top: 3%;
}
.insta-logo img{
  width: 15vw;
}

@media screen and (max-width:620px) {
   .signupCard{
     width: 60%;
   }
   .insta-logo img{
     width: 35%;
   }
}